<template>
    <div class="container">
        <div class="wrap">
            <div class="search">
                <input type="text" class="searchTerm" v-model="search" v-on:keyup="getcharecters"  placeholder="which charecter are you looking for?">
                <button type="submit" class="searchButton">
                    <i class="fa fa-search"></i>
                </button>
            </div>
        </div>
        <div class="row" >
            <div class="column" v-bind:key="hero.id" v-for="hero in heroes">
                <div class="our-team" >
                    <img :src="hero.image.url">
                    <div class="team-content">
                        <h3 class="title">{{hero.name}}</h3>
                        <span class="post">{{hero.biography["full-name"]}}</span>
                        <div class="team-skill">
                            <div class="skill-name">
                                <p>intelligence</p><p>{{hero.powerstats.intelligence}}%</p>
                            </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.intelligence}`">
                                </div>
                            </div>
                            <div class="skill-name">
                                <p>combat</p><p>{{hero.powerstats.combat}}%</p>
                            </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.combat}`">
                                </div>
                            </div><div class="skill-name">
                            <p>durability</p><p>{{hero.powerstats.durability}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.durability}`">
                                </div>
                            </div><div class="skill-name">
                            <p>Power</p><p>{{hero.powerstats.power}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.power}`">
                                </div>
                            </div><div class="skill-name">
                            <p>strength</p><p>{{hero.powerstats.strength}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.strength}`">
                                </div>
                            </div><div class="skill-name">
                            <p>Speed</p><p>{{hero.powerstats.speed}}%</p>
                        </div>
                            <div class="progress">
                                <div :class="`progress-bar w-${hero.powerstats.process}`">
                                </div>
                            </div>
                        </div>
                        <div class="favourite">
                            <link href="//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap.min.css" rel="stylesheet">

                            <label  for="id-of-input" class="custom-checkbox" v-on:click="handleSelectItem(hero.id)">
                                <input type="checkbox" id="id-of-input"/>
                                <i class="glyphicon glyphicon-star-empty"></i>
                                <i class="glyphicon glyphicon-star"></i>
                                <span>Favorite</span>
                            </label>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
        import {access_token,gid} from "@/superhero";
        import axios from "axios";


        export default {
        data(){
            return{
                heroes: [],
                search:"",
                fav:[],
            }
        },

        mounted(){
            this.getcharecters()
        },
        methods: {
            getcharecters: function () {
                if (this.search.length > 2){
                    this.heroes.splice(0)    // avoiding huge number of search results
                    axios.get(`https://superheroapi.com/api.php/${access_token}/search/${this.search}`).then((results)=>{
                        results.data.results.forEach((item)=>{
                            this.heroes.push(item)
                        })
                    }).catch((error)=> console.log(error))
                }
            },
            handleSelectItem: function (id) {
                if(gid.indexOf(id)==-1){
                gid.push(id)
                console.log(id)
                }
            },

        }
    }
</script>

<style scoped lang="scss">
    .demo{ background: #e5e5e5; }
    .our-team{
        border: 2px solid #ff3232;
        border-radius: 10px;
        text-align: center;
        margin: 10px;
        z-index: 1;
        position: relative;
    }
    .our-team:before,
    .our-team:after{
        content: "";
        width: 30%;
        height: 50%;
        background: #ff3232;
        position: absolute;
        top: 50%;
        left: 0;
        z-index: -1;
        transform: translateY(-50%) scaleX(0.3);
        transition: all 0.3s ease 0s;
    }
    .our-team:after{
        width: 106%;
        left: 50%;
        transform: translate(-50%, -50%) scaleY(0.25);
    }
    .our-team:hover:before{ transform: translateY(-50%) scaleX(0.7); }
    .our-team:hover:after{ transform: translate(-50%, -50%) scaleY(0.7); }
    .our-team img{
        width: 100%;
        height: 100%;
        border-radius: 10px;
        transition: all 0.3s ease 0s;
    }
    .our-team .team-content{
        width: 93%;
        padding: 25px 0 10px;
        background: #ff4444;
        position: absolute;
        bottom: 50px;
        left: 50%;
        opacity: 0;
        -webkit-clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
        clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
        transform: translateX(-50%);
        transition: all 0.3s cubic-bezier(0.5, 0.2,0.1,0.9);
    }
    .our-team:hover .team-content{
        bottom: 10px;
        opacity: 1;
    }
    .our-team .title{
        font-size: 25px;
        font-weight: 600;
        color: #fff;
        letter-spacing: 1px;
        text-transform: capitalize;
        margin: 0;
    }
    .our-team .post{
        display: block;
        font-size: 16px;
        color: #fff;
        text-transform: uppercase;
        margin-bottom: 10px;
    }
    .our-team .favourite{
        padding: 0;
        margin: 0;
        list-style: none;
    }
    .our-team .favourite li{
        display: inline-block;
        margin: 0 5px;
    }
    .our-team .favourite li a{
        display: block;
        width: 35px;
        height: 35px;
        line-height: 35px;
        border-radius: 50%;
        background: #fff;
        font-size: 20px;
        color: #ff3232;
        transition: all 0.3s ease 0s;
    }
    .our-team .favourite li a:hover{
        background: linear-gradient(to bottom,#ff3232, darkred);
        box-shadow: 0 0 0 5px rgba(255,255,255,0.3);
        color: #fff;
    }
    @media only screen and (max-width: 990px){
        .our-team{ margin-bottom: 30px; }
    }
    .our-team .skill-name {
        text-align: left;
    }

    .our-team .skill-name p {
        display: inline-block;
        color: #353535;
        font-size: 14px;
        font-weight: 600;
        margin: 0;
    }

    .our-team .skill-name p:last-child {
        float: right;
    }

    .our-team .progress {
        position: relative;
        width: 100%;
        height: 5px;
        margin-bottom: 10px;
        background: #dddddd;
    }

    .our-team .progress:last-child {
        margin: 0;
    }

    .our-team .progress-bar {
        position: absolute;
        width: 0%;
        height: 5px;
        top: 0;
        left: 0;
        background: #454545;
        transition: all .7s;
        z-index: 1;
    }


    .column {
        float: left;
        width: 32%;
        padding: 5px;
    }


    /* Clearfix (clear floats) */

   @for $i from 0 through 100 {
    .our-team:hover .progress-bar.w-#{$i}{
        width: $i*1% ;
    }
       .search
       {
           border: 2px solid #CF5C3F;
           overflow: auto;
           border-radius: 5px;
           -moz-border-radius: 5px;
           -webkit-border-radius: 5px;
       }

       .search input[type="text"]
       {
           border: 0px;
           width: 67%;
           padding: 10px 10px;
       }

       .search input[type="text"]:focus
       {
           outline: 0;
       }

       .search input[type="submit"]
       {
           border: 0px;
           background: none;
           background-color: #CF5C3F;
           color: #fff;
           float: right;
           padding: 10px;
           border-radius-top-right: 5px;
           -moz-border-radius-top-right: 5px;
           -webkit-border-radius-top-right: 5px;
           border-radius-bottom-right: 5px;
           -moz-border-radius-bottom-right: 5px;
           -webkit-border-radius-bottom-right: 5px;
           cursor:pointer;
       }

       /* ===========================
          ====== Medua Query for Search Box ======
          =========================== */

       @media only screen and (min-width : 150px) and (max-width : 780px)

       {}
           .search
           {
               width: 95%;
               margin: 0 auto;
           }


    }
    label {
        /* Presentation */
        font-size: 20px
    }

    /* Required Styling */

    label input[type="checkbox"] {
        display: none;
    }

    .custom-checkbox {
        margin-left: 2em;
        position: relative;
        cursor: pointer;
    }

    .custom-checkbox .glyphicon {
        color: gold;
        position: absolute;
        top: 0.4em;
        left: -1.25em;
        font-size: 0.75em;
    }

    .custom-checkbox .glyphicon-star-empty {
        color: gray;
    }

    .custom-checkbox .glyphicon-star {
        opacity: 0;
        transition: opacity 0.2s ease-in-out;
    }

    .custom-checkbox:hover .glyphicon-star{
        opacity: 0.5;
    }

    .custom-checkbox input[type="checkbox"]:checked ~ .glyphicon-star {
        opacity: 1;
    }
</style>